import { createTab } from '@softomate/rework-party';

export default class AffiliateActivation {
  merchantResetTimers = {};

  merchantActivations = [];

  activationMessageHandler = async ({ request }) => {
    const merchantId = request?.data?.merchantId;
    let merchant;
    if (merchantId) {
      merchant = sdk.coupons.merchants.getById(merchantId);
    } else {
      merchant = await sdk.coupons.merchants.getCurrentMerchant();
    }
    const activationLink = merchant?.activationLink;
    if (!merchant || !activationLink) return;
    this.activateMerchant({ merchantId: merchantId ?? merchant.id, activationLink, merchantDomain: merchant.domain });
  };

  activateMerchant({ merchantId, activationLink, merchantDomain }) {
    const activation = this.merchantActivations.find(a => a.merchantId === merchantId);
    if (activation) {
      return;
    }
    createTab({ url: activationLink, active: false });
    sdk.reporting.internal('activate_merchant', { action_detail: { merchant_name: merchantDomain } });
    sdk.reporting.report('activate_merchant', merchantDomain);

    this.merchantActivations.push({ merchantId, activationLink, time: new Date() });
    this.merchantResetTimers[merchantId] = setInterval(() => {
      clearInterval(this.merchantResetTimers[merchantId]);
      this.merchantActivations = this.merchantActivations.filter(a => a.merchantId !== merchantId);
      delete this.merchantResetTimers[merchantId];
    }, 30 * 60 * 1000);
  }
}
