import addDays from 'date-fns/addDays';
import isAfter from 'date-fns/isAfter';

class LimitedTimeOffer {
  storageStartDateKey = 'limited-time-offer-start-date';

  storageEndDateKey = 'limited-time-offer-end-date';

  getTime = () => {
    const startDate = this.getStartDate();
    const endDate = this.getEndDate();
    const isExpired = isAfter(new Date(), endDate);
    return {
      startDate,
      endDate,
      isExpired
    };
  };

  getStartDate() {
    let start = sdk.storage.get(this.storageStartDateKey);
    if (!start) {
      start = Date.now();
      sdk.storage.set(this.storageStartDateKey, start);
    }
    return new Date(start);
  }

  getEndDate() {
    let end = sdk.storage.get(this.storageEndDateKey);
    if (!end) {
      end = addDays(new Date(), 3).getTime();
      sdk.storage.set(this.storageEndDateKey, end);
    }
    return new Date(end);
  }

  static inject() {
    sdk.limitedTimeOffer = new LimitedTimeOffer();
  }
}

export default LimitedTimeOffer;
