import lodash, { find, merge } from 'lodash';
import { BaseMerchants, getActiveTab } from '@softomate/rework-party';

export default class Merchants extends BaseMerchants {
  constructor() {
    super({
      name: 'merchants',
      url: `${sdk.config.coupons.staging}/merchants?project=pango`,
      updateDelay: 4 * 60 * 60 * 1000
    });
  }

  getById = merchantId => find(this.data, ({ id }) => id === merchantId);

  getMerchantAndState = ({ url, id }) => {
    const merchant = url ? this.getByUrl(url) : this.getById(id);
    const state = merchant && find(this.states, s => s.id === merchant.id);
    return { merchant, state };
  };

  fetchOffers = async ({ url, id }) => {
    const { merchant, state } = this.getMerchantAndState({ url, id });
    if (!merchant) {
      return null;
    }
    const { offersUpdateTime } = state;
    if ((offersUpdateTime || 0) + 20 * 60 * 1000 < Date.now()) {
      const response = await fetch(
        `${sdk.config.coupons.staging}/offers?project=pango&merchantId=${merchant.id}&offerType=COUPON&linkType=EXTENSION&preferLanguage=EN`
      );
      let offers = await response.json();
      offers = offers.filter(({ type }) => type === 'COUPON');
      offers = lodash.uniqBy(offers, 'promoCode');
      merchant.offers = offers;
      state.offersUpdateTime = Date.now();
    }
    return merchant;
  };

  initContentMessageHandler = async ({ sender }) => {
    const { url } = sender;
    const { merchant, state } = this.getMerchantAndState({ url });
    if (merchant) {
      await this.fetchOffers({ id: merchant.id });
    }
    return {
      merchant: merchant ? merge({ showCAA: true }, state, merchant) : null
    };
  };

  getCurrentMerchant = async () => {
    const tab = await getActiveTab();
    if (!tab) {
      return null;
    }
    const { url } = tab;
    const { merchant, state } = this.getMerchantAndState({ url });
    if (merchant) {
      await this.fetchOffers({ id: merchant.id });
      return merge({}, merchant, state);
    }
    return null;
  };
}
