import lodash from 'lodash';

class Messaging {
  constructor() {
    browser.runtime.onMessage.addListener((request, sender, callback) => {
      this.handleRequest(request, sender, callback);
      return true;
    });
  }

  async handleRequest(request, sender, callback) {
    if (!request) {
      return callback(null);
    }
    if (request.from === 'AdBlocker') {
      callback(sdk.adBlocker?.getDomRules(sender.tab.id));
    }

    if (request.method) {
      const resource = lodash.get(sdk, request.method);
      if (!resource) {
        return callback(null);
      }
      if (typeof resource === 'function') {
        try {
          let response;
          if (/.*MessageHandler/.test(request.method)) {
            response = await resource({ data: request.data, sender });
          } else if (request.arguments) {
            /*
             * Handle case with multiple arguments
             * sdk.multiArgument = (a, b, c) => { .... }
             *
             * In Content/Popup
             * chrome.runtime.sendMessage({ method: 'multiArgument', arguments: [1, 2, 3] }, () => { ... })
             *
             * */
            response = await resource(...request.arguments);
          } else {
            /*
             * Handle case with single function argument
             * sdk.simpleMethod = (arg) => { .... }
             *
             * In Content/Popup
             * chrome.runtime.sendMessage({ method: 'simpleMethod', data: { 'a': 1 } }, () => { ... })
             *
             * */
            response = await resource(request.data);
          }
          callback(response);
        } catch (e) {
          error(`error on execute method ${request.method} on sdk`, e);
          callback({ error: e });
        }
      } else {
        callback(resource);
      }
    }
  }
}

export default Messaging;
