class IconController {
  constructor() {
    this.setIcon();

    sdk.proxy.onStatusChanged.add(this.setIcon.bind(this));
  }

  setIcon() {
    const status = sdk.proxy.status.status;
    let icon;

    if (sdk.proxy.getGlobalStatus() === 'unavailable') {
      icon = 'unavailable';
    } else if (status === 'connected') {
      icon = 'connected';
    } else {
      icon = status;
    }

    browser.browserAction.setIcon({
      path: {
        '19': `/static/assets/icons/icon-${icon}-19.png`,
        '38': `/static/assets/icons/icon-${icon}-38.png`
      }
    });
  }
}

export default IconController;
