import Merchants from '@common-shared/Coupons/Merchants';
import CaaConfigs from '@common-shared/Coupons/CaaConfigs';
import TopMerchants from '@common-shared/Coupons/TopMerchants';
import AffiliateActivation from '@common-shared/Coupons/AffilaiteActivation';

class CouponsModule {
  constructor() {
    this.introKey = 'coupons.intro.accepted';
    this.tutorialCompletedKey = 'coupons.tutorial.completed';
    this.merchants = new Merchants();
    this.topMerchants = new TopMerchants();
    this.activations = new AffiliateActivation();
    this.caa = {
      configs: new CaaConfigs()
    };
  }

  isIntroAccepted = () => !!sdk.storage.get(this.introKey);

  onIntroAccept = () => sdk.storage.set(this.introKey, true);

  isTutorialCompleted = () => !!sdk.storage.get(this.tutorialCompletedKey);

  onTutorialComplete = () => sdk.storage.set(this.tutorialCompletedKey, true);

  static async init() {
    const coupons = new CouponsModule();
    try {
      await coupons.merchants.update();
    } catch (e) {
      error('[CouponsModule] Error on merchants.update', e);
    }
    try {
      await coupons.caa.configs.update();
    } catch (e) {
      error('[CouponsModule] Error on caa.configs.update', e);
    }
    try {
      await coupons.topMerchants.update();
    } catch (e) {
      error('[CouponsModule] Error on topOffers.update', e);
    }
    sdk.coupons = coupons;
    return coupons;
  }
}

export default CouponsModule;
