import '@common-shared/SDK';
import 'config';
import Debug from '@common-shared/Wdgt/Debug';

import Storage from '@common-shared/Wdgt/Storage';
import User from '@common-shared/User';
import AntiPhishing from '@common-shared/AntiPhishing';
import Tabs from '@common-shared/Wdgt/Tabs';
import Proxy from '@common-shared/Proxy';
import Reporting from '@common-shared/Reporting';
import Monitoring from '@common-shared/Monitoring';
import DnsPrefetch from '@common-shared/DnsPrefetch';
import WebRTC from '@common-shared/WebRTC';
import Messaging from '@common-shared/Messaging';
import CouponsModule from '@common-shared/Coupons';
import IconBadgeController from '@common-shared/IconBadgeController';
import IconController from './IconController';
import LimitedTimeOffer from './LimitedTimeOffer';

class background {
  constructor() {
    this.init();
  }

  init() {
    new Debug();
    new Messaging();
    IconBadgeController.init();
    CouponsModule.init();
    new Storage().init().then(() => {
      sdk.storage.set('Proxy.proxyLocal', true);
      new User().init();
      new Reporting().init().then(() => {
        new Tabs().init().then(() => {
          new Proxy();
          new Monitoring();
          new IconController();
          new DnsPrefetch();
          AntiPhishing.inject();
          WebRTC.inject();
          LimitedTimeOffer.inject();
          sdk.loading.loaded = true;
        });
      });
    });
  }
}

try {
  new background();
} catch (e) {
  console.log('CRITICAL ERROR');
  console.log(e);
}
