import { getActiveTab } from '@softomate/rework-party';

export default class IconBadgeController {
  updateIconBadge({ url }) {
    clearTimeout(this.callback);
    this.callback = setTimeout(() => {
      const { merchant } = sdk.coupons?.merchants?.getMerchantAndState?.({ url });
      const hasCoupons = merchant?.offers?.filter(({ promoCode }) => !!promoCode)?.length > 0;
      const text = hasCoupons ? '%' : '';
      if (this.text !== text) {
        browser.browserAction.setBadgeText({ text });
        this.text = text;
      }
    }, 500);
  }

  constructor() {
    this.callback = undefined;
    this.text = '';

    browser.browserAction.setBadgeBackgroundColor({ color: '#FF5E5E' });

    browser.tabs.onUpdated.addListener((tabId, changeInfo, { url, active }) => {
      if (active && changeInfo.status === 'complete') {
        this.updateIconBadge({ url });
      }
    });
    chrome.tabs.onActivated.addListener(async () => {
      const { url } = await getActiveTab();
      this.updateIconBadge({ url });
    });
  }

  static init() {
    return new IconBadgeController();
  }
}
