import { getActiveTab } from '@softomate/rework-party';

class AntiPhishing {
  data = [];

  retryInterval = 60 * 10 * 1000; // 10 min

  update = async () => {
    try {
      const response = await fetch('https://cyv.wsidg.com/phishing', {
        headers: {
          Authorization: 'Basic d3NfaWRnOkA6TF1QOERDZVJAfWhzcD8uNUhEN3Z+NFIlLikhSw=='
        }
      });
      const data = await response.json();
      const urls = {};
      data.phishing_urls.forEach(url => {
        urls[url] = 1;
      });
      this.data = urls;
    } catch (e) {
      setTimeout(() => this.update(), this.retryInterval);
    }
  };

  isPhishingUrl = url => {
    try {
      const { hostname, pathname } = new URL(url);
      if (!pathname || pathname === '/') {
        return !!this.data[hostname];
      }
      return !!this.data[`${hostname}${pathname}`];
    } catch (e) {
      return false;
    }
  };

  closeCurrentTab = async () => {
    const tab = await getActiveTab();
    if (!tab?.id) {
      return;
    }
    chrome.tabs.remove(tab.id);
  };

  static inject() {
    const antiPhishing = new AntiPhishing();
    sdk.antiPhishing = antiPhishing;
    antiPhishing.update();
    return antiPhishing;
  }
}

export default AntiPhishing;
